// Функция отключающая активную кнопку при переключении частные/корпоративные клиенты
function disableButton(disable, enable) {
    jQuery(disable).attr('disabled', 'disabled');
    jQuery(enable).removeAttr('disabled');
}

// Функция отключающая активную кнопку и включающая неактивные кнопки при переключении по комплектам услуг
function toggleServiceButtonStatus(buttonToDisable) {
    for (var i = 0; i < actualSelectors.buttonsToShowCategoryOfServices.length; i++) {
        jQuery(actualSelectors.buttonsToShowCategoryOfServices[i]).removeAttr('disabled');
    }
    jQuery(buttonToDisable).attr('disabled', 'disabled');
}

// Фукнция переключения частные/корпоративные клиенты и вакансии клининг/дизенсекция
function togglePersonalCorporate(buttonId, buttonToDisable, displayed, serviceButtonToDisable) {
    jQuery(buttonId).click(function () {
        console.log('click toggle section');
        jQuery('.cleaning-personal').toggle(0);
        jQuery('.cleaning-corporate').toggle(0);
        if (displayedCategoryOfServices !== displayed) {
            jQuery(displayedCategoryOfServices).hide();
            displayedCategoryOfServices = displayed;
            jQuery(displayedCategoryOfServices).show();
        }
        disableButton(this, buttonToDisable);
        toggleServiceButtonStatus(serviceButtonToDisable);
        if (serviceButtonToDisable == "#showOnce") {
            jQuery('.calculatePrice').text('Рассчитать стоимость');
            jQuery('.calculatePrice').attr('id', 'openOnetime');
        } else {
            jQuery('.calculatePrice').text('Оставить заявку');
            jQuery('.calculatePrice').attr('id', 'openGeneralOrder');
        }
    });
}

