// Фукнция изменения отображаемой услуги (внутри комплекта)
function changeShowingService() {
    jQuery('.showService').click(function (event) {
        event.preventDefault();
        console.log('click showService');
        if (jQuery(this).hasClass('actual') == false) {
            jQuery(this).addClass('actual');
        } else {
            jQuery(this).removeClass('actual');
        }
        var showingService = jQuery(this).siblings("p").first(); // Показываем соседний с кнопкой p
        var parent = jQuery(this).parent();
        var categoryOfServices = parent.parent(); // Определяем родительский контейнер для блока услуги
        if (showingService.hasClass('showing-service') !== true) {
            // Если кликаем по непоказанному
            showingService.slideToggle(200); // Отображаем его
            var toHide = categoryOfServices.find('.showing-service'); // Ищем отображенные элементы в родительском контейнере
            toHide.slideToggle(200); // Скрываем другие отображённые элементы
            toHide.removeClass('showing-service'); // Удаляем класс showing-service со скрытых элементов
            toHide.siblings("button").first().removeClass('actual');
            showingService.addClass('showing-service'); // Добавляем класс showing-service на показанный элемент
        } else {
            showingService.slideToggle(200, function () {
                // Если кликают по показанному элементу
                jQuery(this).removeClass('showing-service'); // то он скрывается
            });
        }
    });
}

